<template>
    <v-expansion-panel-content>
        <div slot="header"><b>{{terminal.name}}</b></div>
        <v-card extended>
            <img :src="'/terminal/'+ terminal.id +'/auth.png'">
            <v-autocomplete
                    v-model="model"
                    :items="groups"
                    :disabled="!isEditing"
                    box
                    chips
                    color="blue-grey lighten-2"
                    label="Группы"
                    item-text="name"
                    item-value="name"
                    multiple
            >
                <template
                        slot="selection"
                        slot-scope="data"
                >
                    <v-chip
                            :selected="data.selected"
                            :close="isEditing"
                            class="chip--select-multi"
                            @input="data.parent.selectItem(data.item)"
                    >
                        {{ data.item.name }}
                    </v-chip>
                </template>
                <template
                        slot="item"
                        slot-scope="data"
                >
                    <template v-if="typeof data.item !== 'object'">
                        <v-list-tile-content v-text="data.item"></v-list-tile-content>
                    </template>
                    <template v-else>
                        <v-list-tile-content hint="123123">
                            <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                            <v-list-tile-sub-title v-html="data.item.group"></v-list-tile-sub-title>
                        </v-list-tile-content>
                    </template>
                </template>

            </v-autocomplete>

            <v-btn
                    color="blue darken-3"
                    fab
                    small
                    absolute
                    bottom
                    right
                    @click="save_input"
            >
                <v-icon v-if="isEditing">done</v-icon>
                <v-icon v-else>edit</v-icon>
            </v-btn>
        </v-card>
    </v-expansion-panel-content>


</template>

<script>
    import {mapActions, mapState} from 'vuex'
    import * as types from '../types'
    import BuildingCard from "./BuildingCard";

    function searchById(id, array) {
        for (var i = 0; i < array.length; i++) {

            if (array[i].id === id) {
                return array[i];
            }
        }
    }

    function searchByName(name, array) {
        for (var i = 0; i < array.length; i++) {
            if (array[i].name === name) {
                return array[i];
            }
        }
    }

    Array.prototype.clear = function () {
        if(this.length>0) this.splice(0, this.length);
    };
    export default {
        name: "Terminal",
        components: {BuildingCard},
        props: [
            'terminal'
        ],
        methods: {
            ...mapActions({
                groupsRemove: types.GROUP_REMOVE,
                terminalSet: types.TERMINAL_SET
            }),
            building_change: function (building) {
                this.group = Object.assign(this.group, building);
                this.groupSet(this.group)
            },
            save_input: function () {
                this.isEditing = !this.isEditing
                if (!this.isEditing) {
                    this.terminal.groups = [];
                    this.model.map(entry => this.terminal.groups.push(searchByName(entry, this.groups).id))
                    this.terminalSet(this.terminal)
                }
            },
            mouseLeave: function () {
                setTimeout(() => this.hidden = true, 800)

            },
            removeAction() {
                this.groupsRemove({name: this.group.name})
            }

        },
        data() {
            return {
                isEditing: false,
                hidden: false,
                model: [],
                isOpen: false,
            }
        },
        computed: {
            ...mapState({
                groups: state => state.terminals.groups
            })
        },
        mounted() {
            this.hidden = false;
            if (this.terminal.groups) this.terminal.groups.map(entry => this.model.push(searchById(entry, this.groups).name));
        }

    }
</script>

