<template>
    <v-card
            color="blue-grey darken-3"
            dark
    >
        <v-card-text v-if="show_input">
            <v-autocomplete
                    v-model="model"
                    :items="items"
                    :loading="isLoading"
                    :search-input.sync="search"
                    color="white"

                    item-text="building_name"
                    item-value="API"
                    label="Учреждение"
                    placeholder="Введите учреждение"
                    prepend-icon="mdi-database-search"
                    return-object
                    @change="change_input"
                    ></v-autocomplete>
        </v-card-text>
        <v-divider></v-divider>
        <v-expand-transition>
            <v-list  v-if="!show_input" class="blue-grey darken-2" dense>
                <v-list-tile> <small>Учреждение</small> </v-list-tile>

                <v-list-tile
                        v-for="(field, i) in fields"
                        :key="i"  v-if="field.key === 'building_name' ||  field.key === 'building_address'">
                    <v-list-tile-content>
                        <v-list-tile-sub-title v-if="field.key === 'building_address'" v-text="field.value"></v-list-tile-sub-title>
                        <v-list-tile-title v-if="field.key === 'building_name'" v-text="field.value"></v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
                <v-btn
                        :disabled="!model"
                        color="grey darken-3"
                        @click="clear_input"
                >
                    Сбросить
                    <v-icon right>eject</v-icon>
                </v-btn>
            </v-list>
        </v-expand-transition>
        <v-spacer></v-spacer>
    </v-card>
</template>

<script>

    import axios from 'axios'

    export default {
        props: [
            'building'
        ],
        data: () => ({
            descriptionLimit: 60,
            entries: [],
            isLoading: false,
            model: null,
            search: null,
            show_input: false
        }),
        methods:{
            change_input: function (data) {
                this.$emit('change-building', {building_id: data.building_id, building_name: data.building_name, building_address: data.building_address})
                this.show_input = false;
            },
            clear_input: function () {
                this.model = null;
                this.building_id = null;
                this.$emit('change-building', {building_id: null, building_name: null, building_address: null});
                this.show_input = true;
            }
        },
        computed: {
            fields () {
                if (!this.model) return [];

                return Object.keys(this.model).map(key => {
                    return {
                        key: key,
                        value: this.model[key] || 'n/a'
                    }
                })
            },
            items () {
                return this.entries.map(entry => {

                    entry.building_id=entry.id;
                    entry.building_name=entry.title;
                    entry.building_address=entry.address;
                    /*const Description = entry.title.length > this.descriptionLimit
                        ? entry.title.slice(0, this.descriptionLimit) + '...'
                        : entry.title+ " (id"+ entry.id+ ")";

                    return Object.assign({}, entry, { Description })*/
                    return entry;
                })
            }
        },

        watch: {
            search (val) {
                // Items have already been loaded
                if (this.items.length > 0) return

                this.isLoading = true

                // Lazily load input items
                axios.get('/buildings')
                    .then(res => {
                        this.entries = res.data
                        console.log(this.items)
                    })
                    .catch(err => {
                        console.log(err)
                    })
                    .finally(() => (this.isLoading = false))
            }
        },

        mounted(){
            this.model=this.building;
            if (this.building.building_id === 0) this.show_input = true

        }
    }
</script>

