<template>
  <v-container> <v-text-field
          v-model="search"
          append-icon="search"
          label="Фильтр"
          single-line
          hide-details
          clearable
  ></v-text-field>
  <v-data-table
          :loading="isLoading"
          :items="logs"
          :headers="headers"
          :pagination.sync="pagination"
          :search="search"
          select-all
          item-key="id"
  >
    <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
    <template slot="headers" slot-scope="props">
      <tr>
        <th
                v-for="header in props.headers"
                :key="header.text"
                :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                @click="changeSort(header.value)"
        >
          <v-icon small>arrow_upward</v-icon>
          {{ header.text }}
        </th>
      </tr>
    </template>
    <template slot="items" slot-scope="props">
      <tr :active="props.selected" :class="{ errorItem: isErrorCode(props.item.code)}">
        <td class="text-xs-left"><small class="grey darken-2">{{frontEndDateFormat(props.item.dt)}}</small><br> {{frontEndDateFromNow(props.item.dt)  }}</td>
        <td class="text-xs-right">{{ props.item.data }}</td>
        <td class="text-xs-left">{{ props.item.message }}</td>
        <td class="text-xs-right" >{{ props.item.code }}</td>

      </tr>
    </template>
  </v-data-table>
  </v-container>
</template>

<script>
    import {mapActions} from 'vuex'
    import * as types from '../types'
    import api from '../api/api'
    import moment from 'moment'
    export default {
        name: "Logs",
        methods: {
            frontEndDateFormat: function(date) {
                moment.locale("ru");
                return moment.unix(date).format('DD/MM/YYYY HH:mm');
            },
            frontEndDateFromNow: function(date) {
                return moment.unix(date).fromNow();
            },
            changeSort (column) {
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
            },
            isErrorCode: function (code) {
                let codes = [401, 402];
                let idx = codes.indexOf(code);
                return (idx>=0)
            },
            ...mapActions({
                groupsRemove: types.GROUP_REMOVE,
                groupSet: types.GROUP_SET
            }),
            getLogs: function () {
                this.isLoading = true;
                api.getLogs()
                    .then(logs => {
                        this.logs = logs.data;
                        this.isLoading = false
                    })
                    .catch( err => {
                        this.isLoading = false
                        console.log(err)
                    });

            }

        },
        data() {
            return {
                search: '',
                isLoading: false,
                hidden: false,
                isOpen: false,
                pagination: {
                    sortBy: 'dt',
                    descending: true,
                    rowsPerPage: 25,
                },
                headers: [
                    {   text: 'Датa',
                        value: 'dt',
                        align: 'center' ,
                        sort: 'desc'
                    },
                    {
                        text: 'Данные',
                        align: 'left',
                        value: 'data',
                        width: '10%'
                    },
                    {   text: 'Сообщение',
                        value: 'message',
                        width: '90%',
                        align: 'right'
                    },
                    {   text: 'Код',
                        value: 'code'
                    }
                ],
                logs: [],

            }

        },

        mounted(){
            this.hidden = false;
            this.getLogs()
        }

    }
</script>
<style >
  .errorItem{
    background-color: #492421;
  }
  table.v-table tbody td,
  table.v-table tbody td:first-child {
  padding: 0 5px !important;
  }


</style>