
export default {
    last_color: "",
    colors: ["red", "pink", "purple", "indigo", "blue", "teal", "green", "lime", "yellow", "orange", "brown"],
    random_color(){
        return this.colors[Math.floor(Math.random()*this.colors.length)];
    },
    get_color(){
        let rez = this.random_color();
        if(rez === this.last_color) return this.get_color();
        this.last_color = rez;
        return rez
    }
}