<template>
    <v-container fluid>
        <v-flex xs12>
            <v-autocomplete
                    v-model="friends"
                    :disabled="isUpdating"
                    :items="dbs"
                    box
                    chips
                    color="blue-grey lighten-2"
                    label="Базы"
                    item-text="name"
                    item-value="name"
                    multiple
            >
                <template
                        slot="selection"
                        slot-scope="data"
                >
                    <v-chip
                            :selected="data.selected"
                            close
                            class="chip--select-multi"
                            @input="data.parent.selectItem(data.item)"
                    >
                        {{ data.item.name }}
                    </v-chip>
                </template>
                <template
                        slot="item"
                        slot-scope="data">
                    <template v-if="typeof data.item !== 'object'">
                        <v-list-tile-content v-text="data.item"></v-list-tile-content>
                    </template>
                    <template v-else>
                        <v-list-tile-content>
                            <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                        </v-list-tile-content>
                    </template>
                </template>
            </v-autocomplete>
        </v-flex>
        <v-flex xs12>
            <v-textarea
                    auto-grow
                    name="input-7-1"
                    label="SQL запрос"
                    v-model="sql_query"
                    hint="Внимательно проверьте запрос перед выполнением"
            ></v-textarea>
        </v-flex>
        <v-btn color="success" @click="doQuery">Выполнить</v-btn>
        <v-data-table
                :items="JsonResult"
                :headers="headers_data"
                hide-actions
                class="elevation-1"
                :loading="isProcess">
            <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
            <template slot="items" slot-scope="props">
                <td v-for="item in props.item" v-bind:key="item.name">
                    {{item}}
                </td>

            </template>
            <template slot="no-data">
                <div class="text-xs-center">Нет данных</div>
            </template>
        </v-data-table>

    </v-container>
</template>

<script>
    import {mapState, mapActions} from 'vuex'
    import * as types from '../types'


    export default {
        name: "SqlRunner",
        data() {
            return {
                isLoading: false,
                headers: [],
                isUpdating: false,
                sql_query: 'SELECT "OrgName","OrgId","INN" FROM "Org"',
                friends: ['Sandbox', 'Test'],
                dbs: [

                ]
            }
        },
        computed: {
            ...mapState({
                results: state => state.sqlrunner.results,
                isProcess: state => state.sqlrunner.isProcess
            }),
            JsonResult: function () {
                let rez;
                try {
                    this.results.map((item, index) =>{

                        let slice = JSON.parse(item);


                        if(typeof rez === 'undefined') rez = slice
                        else {
                            rez = [...rez, ...slice];
                        }
                        rez.push({server_alias:this.friends[index]});

                    });

                }
                catch (e) {
                }

                return rez
            },
            headers_data: function () {
                if (this.JsonResult != null) {
                    let rez = [];
                    let keys = Object.keys(this.JsonResult[0]);
                    keys.map(value => rez.push({text: value, vaule: value}))
                    return rez
                }
                else {
                    return [{text: '', vaule: ''}]
                }

            }
        },
        methods: {

            test() {

                console.log(this.results)
                console.log(this.headers_data)
            }
            ,
            doQuery() {
                this.isLoading = true;
                let conn = [];
                this.friends.map((item) => conn.push(this.dbs.find(i => i.name === item).connString))
                conn.map(item => this.sqlQuery({constring: item, query: this.sql_query}))
            }
            ,
            ...
                mapActions({
                    sqlQuery: types.SQL_QUERY
                })
        }
        ,
        watch: {
            isUpdating(val) {
                if (val) {
                    setTimeout(() => (this.isUpdating = false), 3000)
                }
            }
        }

    }
</script>

