<template>
    <v-container grid-list-md>
    <v-layout wrap>
        <v-flex sm12>
            <v-toolbar dense>
                <v-toolbar-title>Группы</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn flat color="green" @click.native="showDialog">
                        <v-icon>add</v-icon>
                    </v-btn>
                </v-toolbar-items>
            </v-toolbar>

            <v-expansion-panel >
                <terminal-group class="column" v-for="item in groups" :key="item.name" :group="item" lazy></terminal-group>
            </v-expansion-panel>


            <v-dialog v-model="dialog" max-width="500px">
                <v-card>
                    <v-card-text>
                        <v-text-field ref="groupNameInput" v-model="groupName" label="Название группы"
                                      @keyup.enter.native="addGroup"></v-text-field>
                        <small class="grey--text">* Название должно быть уникальным</small>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn :disabled="groupName.length ==0" flat color="primary" @click.native="addGroup">Добавить
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-flex>
        <v-flex sm12>
            <v-toolbar dense>
                <v-toolbar-title>Терминалы</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                    <v-btn flat @click.native="showDialogTerm">
                        <v-icon>add</v-icon>
                    </v-btn>
                </v-toolbar-items>
            </v-toolbar>
            <v-expansion-panel >
                <terminal class="column" v-for="item in terminals" :key="item.name" :terminal="item"></terminal>
            </v-expansion-panel>

            <v-dialog v-model="dialogTerm" max-width="500px">
                <v-card>
                    <v-card-text>
                        <v-text-field ref="terminalNameInput" v-model="terminalName" label="Название терминала"
                                      @keyup.enter.native="addTerminal"></v-text-field>
                        <small class="grey--text">* Название терминала должно быть уникальным</small>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn :disabled="terminalName.length ==0" flat color="primary" @click.native="addTerminal">Добавить
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

        </v-flex>
    </v-layout>
    </v-container>
</template>

<script>
    import TerminalGroup from "../components/TerminalGroup";
    import Terminal from "../components/Terminal";
    import {mapState, mapActions} from 'vuex'
    import * as types from '../types'

    export default {
        name: "terminals",
        components: {TerminalGroup, Terminal},
        data() {
            return {
                dialog: false,
                dialogTerm: false,
                groupName: '',
                terminalName: ''
            }
        },
        computed: {
            ...mapState({
                groups: state => state.terminals.groups,
                terminals: state => state.terminals.terminals
            })
        },
        methods: {
            addGroup: function () {
                this.dialog = !this.dialog;
                this.groupAdd({name: this.groupName})

            },
            addTerminal: function () {
                this.dialogTerm = !this.dialogTerm;
                this.terminalAdd({name: this.terminalName})

            },
            showDialog: function () {
                this.groupName = '';
                this.dialog = !this.dialog;
                this.$nextTick(this.$refs.groupNameInput.focus)
            },
            showDialogTerm: function () {
                this.terminalName = '';
                this.dialogTerm = !this.dialogTerm;
                this.$nextTick(this.$refs.terminalNameInput.focus)
            },
            ...mapActions({
                groupsGet: types.GROUPS_GET,
                terminalsGet: types.TERMINALS_GET,
                terminalAdd: types.TERMINAL_ADD,
                groupAdd: types.GROUP_ADD
            })
        },
        created() {
            this.groupsGet();
            this.terminalsGet();
        }
    }
</script>

