import Vue from 'vue'
import Router from 'vue-router'
import About from './views/About.vue'
import Logs from './views/Logs.vue'
import Settings from './views/Settings.vue'
import Stats from './views/Stats.vue'
import Check from './views/Check.vue'
import Upload from './views/Upload.vue'
import Terminals from './views/Terminals.vue'
import SqlRunner from './views/SqlRunner.vue'
import store from './store'

Vue.use(Router);

function requireAuth (to, from, next) {
    console.log(store.state.authorization.isAuth)
    if (store.state.authorization.isAuth) {
        store.commit('');
        next({
            path: '/',
            query: { redirect: to.fullPath }
        })
    } else {
        next({path: '/login'})
    }
}

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'home',
            title: 'СКД',
            component: Stats,
            meta: { title: 'SKD' },
        },
        {
            path: '/about',
            name: 'about',
            component: About,
           // beforeEnter: requireAuth
        },
        {
            path: '/logs',
            name: 'logs',
            component: Logs,
            meta: {
                requiresAuth: true,
                title: 'Logs'
            }
        },
        {
            path: '/upload',
            name: 'upload',
            component: Upload,
            meta: {
                requiresAuth: true,
                title: 'Logs'
            }
        },
        {
            path: '/terminals',
            name: 'terminals',
            component: Terminals,
            meta: {
                requiresAuth: true,
                title: 'Terminal'
            }
        },
        {
            path: '/check',
            name: 'check',
            component: Check,
            meta: {
                title: 'Check Ticket'
            }
        },
        {
            path: '/settings',
            name: 'settings',
            component: Settings,
            meta: {
                requiresAuth: true,
                title: 'Settings'
            }
        },
        {
            path: '/stat',
            name: 'stat',
            component: Stats,
            meta: {
                title: 'Статистика'
            }
        },
        {
            path: '/sqlrunner',
            name: 'sqlrunner',
            component: SqlRunner,
            meta: {
                requiresAuth: true
            }
        },
    ]
})
