import * as types from '../types'
import api, {API_RESPONSE_OK_CODE, API_UNAUTHORIZED_STATUS} from '../api/api'

const state = {
    isAuth: false,
    exp: null,
    apiErr: null,
    isInit: false
};

function logoff(state) {
    state.isAuth = false;
    state.exp = null;
    localStorage.setItem('authorization', JSON.stringify(state));
}
const mutations = {

    initialiseStore(state) {
        state.isInit = true;
        if(localStorage.getItem('authorization')) {
            // Replace the state object with the stored item
            let localstor = JSON.parse(localStorage.getItem('authorization'))
            if (Date.parse(localstor.exp) > Date.now()){
                Object.assign(state, localstor)
            }
        }
    },
    [types.MUTATE_LOGIN_SET]: (state, login) => {
        if(login.exp * 1000 > Date.now()) {

            state.exp = new Date(login.exp * 1000);
            state.isAuth = true;
            localStorage.setItem('authorization', JSON.stringify(state));
        }
       },
    [types.MUTATE_LOGOUT_SET]: (state, logout) => {
        //LocalStorage problem
        if(logout === null) {
            logoff(state);
            return
        }
        //OK
        if(logout.code === API_RESPONSE_OK_CODE) {
            logoff(state);
        }
    },
    [types.MUTATE_API_ERROR]: (state, err) => {
        state.apiErr = err;
        if(state.isAuth && err.response.status === API_UNAUTHORIZED_STATUS ){
           logoff(state);
        }
    }
};

const actions = {
    [types.LOGIN]: ({commit},login) => {
        api.login(login)
            .then(login => {
            commit(types.MUTATE_LOGIN_SET, login.data);
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    },
    [types.LOGOUT]: ({commit}) => {
        api.loguot()
            .then(logout => {
                commit(types.MUTATE_LOGOUT_SET, logout.data);
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    }
};


export default {
    state,
    mutations,
    actions
};
