import * as types from '../types'
import api from '../api/api'

const state = {
    results: [],
    isProcess: false
};
const mutations = {
    [types.SQL_QUERY_RESULT]: (state, result) => {
       state.results.push(result)
        state.isProcess = false
    },
    [types.SQL_QUERY_PREPARE]: (state) => {
        state.isProcess = true;
        state.results = []
    }
};
const actions = {
    [types.SQL_QUERY]: ({commit}, payload) => {
        commit(types.SQL_QUERY_PREPARE);
        api.sqlQuery(payload)
            .then(result => {
                commit(types.SQL_QUERY_RESULT, result.data);
            })
            .catch( err => {
                console.log(err)
            });
    },
};

export default {
    state,
    mutations,
    actions
};