<template>
    <v-expansion-panel-content >
        <div slot="header" >{{group.name}}</div>
        <v-card @mouseleave="mouseLeave" @mouseover="mouseOver" >

            <building-card v-on:change-building="building_change" :building="group"></building-card>
        </v-card>
        <v-text-field
                  v-model="search"
                  append-icon="search"
                  label="Фильтр"
                  single-line
                  hide-details
                  clearable
                ></v-text-field>
        <v-data-table
                :loading="isLoading"
                :items="events"
                :headers="headers"
                :pagination.sync="pagination"
                :search="search"
                select-all
                item-key="id"
                class="elevation-1"
        >
            <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
            <template slot="headers" slot-scope="props">
                <tr>
                <th><v-btn flat icon disabled >
                    <v-icon color="green">loop</v-icon>
                </v-btn></th>

                    <th
                            v-for="header in props.headers"
                            :key="header.text"
                            :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                            @click="changeSort(header.value)"
                    >
                        <v-icon small>arrow_upward</v-icon>
                        {{ header.text }}
                    </th>
                </tr>
            </template>
            <template slot="items" slot-scope="props">
                <tr :active="props.selected" @click="ping(props)">
                    <td> <v-btn flat icon @click.native="test(props.item.id)" :disabled="props.item.last_update === 'sync'">
                        <v-icon small color="green">loop</v-icon>
                    </v-btn>
                    </td>
                    <td class="text-xs-left"><small class="grey darken-2">{{frontEndDateFormat(props.item.dt)}}</small> {{frontEndDateFromNow(props.item.dt)  }}</td>
                    <td>
                        {{ props.item.title }}  <span class="extPlaceCount"></span>
                    </td>
                    <td class="text-xs-center"><small>{{ props.item.hall }}</small></td>
                    <td class="text-xs-left"><small class="grey darken-2" v-if="props.item.last_update !== 'sync'">{{frontEndDateFromNow(props.item.last_update)  }} </small> </td>

                </tr>
            </template>
            <template slot="expand" slot-scope="props">
                <v-card >
                    <v-card-text>
                        Продано: <span v-if="event_info[props.item.id]" class="atlasPlaceCount">  {{event_info[props.item.id].Tickets.tickets}}</span>
                        Прошло: <span v-if="event_info[props.item.id]" class="entriesPlaceCount">  {{event_info[props.item.id].Entries.entries}}</span>
                    </v-card-text>
                </v-card>

            </template>
        </v-data-table>
    </v-expansion-panel-content>


</template>

<script>
    import {mapActions} from 'vuex'
    import * as types from '../types'
    import BuildingCard from "./BuildingCard";
    import api from '../api/api'
    import moment from 'moment'
    export default {
        name: "TerminalGroup",
        components: {BuildingCard},
        props: [
            'group'
        ],
        methods: {
            ping: function(props){
                props.expanded = !props.expanded;
                api.getEventInfo(props.item.id)
                    .then(info => {
                        console.log(info.data);
                        this.$set(this.event_info, props.item.id, info.data);
                    })
            },
            test: function(id) {
                this.syncids.push(id);
                this.isLoading = true;
                this.events.filter(item => item.id === id).map(it => it.last_update="sync");
                api.syncEvent(id)
                    .then(event => {
                        this.events.filter(item => item.id === event.data.id).map(item => {
                            let index = this.syncids.indexOf(id);
                            if (index > -1) {
                                this.syncids.splice(index, 1);
                            }
                            if(this.syncids.length === 0) this.isLoading = false;
                            item.last_update = event.data.last_update;
                            item.tickets_cached = event.data.tickets_cached;
                            return item
                        });
                    })
                    .catch( err => {
                        this.isLoading = false;
                        console.log(err)
                    });
                console.log(id)
            },
            frontEndDateFormat: function(date) {
                moment.locale("ru");
                return moment.unix(date).format('DD/MM/YYYY HH:mm');
            },
            frontEndDateFromNow: function(date) {
                return moment.unix(date).fromNow();
            },
            changeSort (column) {
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
            },
            ...mapActions({
                groupsRemove: types.GROUP_REMOVE,
                groupSet: types.GROUP_SET
            }),
            building_change: function (building){
                if (building.building_id === null) {
                    this.events = [];
                    return
                }
                this.isLoading = true;
                this.group = Object.assign(this.group, building);
                this.groupSet(this.group);
                setTimeout(() => { this.getEvents() }, 1300)
            },
            mouseOver: function(){
                this.hidden = false;
            },
            mouseLeave: function(){
                setTimeout(()=> this.hidden = true, 800)

            },
            removeAction(){
                this.groupsRemove({name: this.group.name})
            },
            getEvents: function () {
                this.isLoading = true;
                api.getEventsByGroupId(this.group.id)
                .then(events => {
                    console.log(events.data)
                    this.events = events.data.events.slice()
                    this.isLoading = false
                })
                .catch( err => {
                    this.isLoading = false
                    console.log(err)
                });

            }

        },
        data() {
            return {

                event_info: [],
                syncids: [],
                search: '',
                isLoading: false,
                hidden: false,
                isOpen: false,
                pagination: {
                    sortBy: 'dt'
                },
                headers: [
                    { text: 'Датa', value: 'dt', align: 'left', width: '100px' },
                    {
                        text: 'Название мероприятия',
                        align: 'left',
                        value: 'title',
                        width: '90%'
                    },
                    { text: 'Зал', value: 'hall', align: 'right', },
                    { text: 'Актуальность данных', value: 'last_update' }
                ],
                events: [],

            }

        },

        mounted(){
            this.hidden = false;
            this.getEvents()
        }

    }
</script>

<style scoped>
    .atlasPlaceCount{
        background-color: #074207;
        margin-right: 10px;
    }
    .entriesPlaceCount{
        background-color: #2e2f7e;
        margin-right: 10px;
    }
    .extPlaceCount{
        margin-left: 10px;
        background-color: #12412b;
    }

</style>