<template>
    <div>
        <v-card class="mb-3 card-event" v-for="event in events" :key="event.id">
            <v-card-title primary-title>
                <div>
                    <div class="headline pb-1"><span class="date">{{frontEndDateFormat(event.dt)}}</span>&nbsp;{{event.title}}</div>


                </div>
            </v-card-title>
            <v-card-text>
                <v-data-table
                        :headers="headers"
                        :items="event.info"
                        class="elevation-1"
                        :hide-actions="true"
                >
                    <template v-slot:items="props">
                        <td class="light-green--text">{{ props.item.id }}</td>
                        <td class="text-xs">{{ props.item.sell }}</td>
                        <td class="text-xs">{{ props.item.total }}</td>
                        <td class="text-xs">{{ props.item.entry }}</td>
                    </template>

                    <template v-slot:footer>
                        <td><strong>Итого</strong></td>
                        <td class="text-xs"><b>{{event.sell}}</b></td>
                        <td class="text-xs"><b>{{event.total}}</b></td>
                        <td class="text-xs"><b>{{event.entry}}</b></td>
                    </template>
                </v-data-table>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
    import color from "../colors"
    import moment from 'moment'
    export default {
        name: 'EventsStatsTimeline',
        props: [
            'events'
        ],
        data: () => ({
            headers: [
                {
                    text: 'Цена',
                    align: 'left',
                    sortable: true,
                    value: 'id'
                },
                { text: 'Продано', value: 'sell', sortable: false},
                { text: 'Сумма', value: 'total', sortable: false},
                { text: 'Прошло', value: 'entry',  sortable: false}
            ]


        }),
        methods:{
            frontEndDateFormat: function(date) {
                moment.locale("ru");
                return moment.unix(date).format('DD.MM.YY  HH:mm ');
            },
            get_color: function () {
                return color.get_color()
            },
            percent: function (event) {
                return (event.entry / event.sell) * 100
            }
        },
        mounted(){
        }
    }
</script>

