import * as types from '../types'
import api from '../api/api'

const state = {
    groups: null,
    terminals: null
};
const mutations = {
    [types.MUTATE_TERMINALS_GROUPS]: (state, groups) => {
        state.groups = groups.groups
    },
    [types.MUTATE_TERMINALS]: (state, terminals) => {
        state.terminals = terminals.terminals
    }
};
const actions = {
    [types.GROUPS_GET]: ({commit}) => {
        api.getGroups()
            .then(groups => {
                commit(types.MUTATE_TERMINALS_GROUPS, groups.data);
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    },
    [types.TERMINALS_GET]: ({commit}) => {
        api.getTerminalss()
            .then(terminals => {
                commit(types.MUTATE_TERMINALS, terminals.data);
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    },
    [types.TERMINAL_SET]: ({commit, dispatch},terminal) => {
        api.setTerminal(terminal)
            .then(() => {
                dispatch(types.TERMINALS_GET)
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    },
    [types.TERMINAL_ADD]: ({commit, dispatch},terminal) => {
        api.addTerminal(terminal)
            .then(() => {
                dispatch(types.TERMINALS_GET)
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    },
    [types.GROUP_ADD]: ({commit, dispatch},group) => {
        api.addGroup(group)
            .then(() => {
                dispatch(types.GROUPS_GET)
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    },
    [types.GROUP_SET]: ({commit, dispatch},group) => {
        api.setGroup(group)
            .then(() => {
                dispatch(types.GROUPS_GET)
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    },
    [types.GROUP_REMOVE]: ({commit, dispatch},group) => {
        api.removeGroup(group)
            .then(() => {
                dispatch(types.GROUPS_GET)
            })
            .catch( err => {
                commit(types.MUTATE_API_ERROR, err);
            });
    },
};

export default {
    state,
    mutations,
    actions
};