<template>
    <nav>
        <v-toolbar
                color="indigo" dense fixed clipped-left app>
                <v-toolbar-side-icon  @click.stop="drawer = !drawer"></v-toolbar-side-icon>


            <v-toolbar-title :to="{path:'/about'}" ><b>kassy.ru </b>
                <small> | </small> <small class="captionText"> <span class="upperLetter">С</span>истема <span class="upperLetter">К</span>онтроля <span class="upperLetter">Д</span>оступа </small>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn v-if="!isAuth" flat @click.stop="login_dialog = true">
                <v-icon>account_box</v-icon>
                &nbsp;Авторизация
            </v-btn>
            <v-btn v-else flat @click.stop="logout">
                <v-icon></v-icon>
                &nbsp;Выйти
            </v-btn>
        </v-toolbar>
        <v-navigation-drawer v-model="drawer" clipped fixed app dark>
            <v-list dense>
                <v-list-tile v-for="item in items" :key="item.text" @click="" :to="item.path" v-if="!item.auth || isAuth">
                    <v-list-tile-action >
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content >
                        {{item.text}}
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>

        </v-navigation-drawer>
        <v-snackbar
                v-model="snackbar"
                :color="snackbar_color"
                :multi-line="mode === 'multi-line'"
                :timeout="timeout"
                :vertical="mode === 'vertical'">
            {{ snackbar_msg}}
            <v-btn
                    dark
                    flat
                    @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
        <v-dialog v-model="login_dialog" max-width="400px">
            <v-card>
                <v-card-title>
                    <span class="headline">Авторизация</span>
                </v-card-title>
                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12>
                                <v-text-field v-model="login_input" label="Email" required></v-text-field>
                            </v-flex>
                            <v-flex xs12>
                                <v-text-field v-model="password_input" label="Password" type="password"
                                              required></v-text-field>
                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="grey" flat @click.native="login_dialog = false">Закрыть</v-btn>
                    <v-btn color="green" flat @click.native="doLogin">Войти</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </nav>
</template>

<script>
    import {mapState, mapActions} from 'vuex'
    import * as types from '../types'

    export default {
        name: "NavBar",
        data: () => ({
            drawer: false,
            items: [
                {icon: 'trending_up', text: 'Терминалы', path: '/terminals', auth: true},
                {icon: 'find_in_page', text: 'Проверка билета', path: '/check', auth: false},
                {icon: 'cloud_upload', text: 'Импорт', path: '/upload', auth: true},
                {icon: 'history', text: 'Логи', path: '/logs', auth: true},
                {icon: 'settings', text: 'Настройки', path: '/settings', auth: true},
                {icon: 'blur_linear', text: 'Статистика', path: '/stat', auth: false}
            ],
            login_dialog: false,
            login_input: '',
            password_input: '',
            snackbar: false,
            snackbar_msg: '',
            timeout: 6000,
            mode: 'multi-line',
            snackbar_color: 'error'
        }),
        methods: {
            doLogin: function () {
                this.login_dialog = false;
                this.login({login: this.login_input, password: this.password_input});
            },
            ...mapActions({
                logout: types.LOGOUT,
                login: types.LOGIN
            })

        },
        computed: {
            ...mapState({
                isAuth: state => state.authorization.isAuth,
                exp: state => state.authorization.exp,
                apiErr: state => state.authorization.apiErr
            })
        },
        watch: {
            //Toast error
            apiErr() {
                this.snackbar_msg = this.apiErr.response.status + " " + this.apiErr.response.statusText
                this.snackbar = true
            },
            isAuth(auth){
                this.drawer = auth;
            }

        },
        mounted() {
            //this.login_dialog = !this.isAuth;
        }

    }
</script>

<style scoped>
    .captionText {
        color: rgba(186, 188, 215, 0.93);
    }
    .upperLetter{
        color: white;
    }
</style>
