<template>
  <v-card
          class="mx-auto pt-5"
          max-width="600"
  >
    <v-card-text class="py-0">

      <v-layout row wrap>
        <v-flex xs12 sm6 md4>
        <v-img
                :src="require(`@/assets/oceanarium.svg`)"
                width="130"
                class="mt-2 ml-2"
        ></v-img>
        </v-flex>
        <v-flex xs12 sm6 md4>
          <v-menu
                  v-model="menu"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                      label="Начало периода"
                      prepend-icon="event"
                      readonly
                      v-on="on"
                      :value="fromDateFormattedMomentjs"
              ></v-text-field>
            </template>
            <v-date-picker v-model="fromDate" @change="getStats" @input="menu = false" locale="ru"></v-date-picker>
          </v-menu>
        </v-flex>
        <v-flex xs12 sm6 md4>
          <v-menu
                  v-model="menu2"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  lazy
                  transition="scale-transition"
                  offset-y
                  full-width
                  min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                      :value="toDateFormattedMomentjs"
                      label="Конец периода"
                      prepend-icon="event"
                      readonly
                      v-on="on"
              ></v-text-field>
            </template>
            <v-date-picker v-model="toDate" @change="getStats" @input="menu2 = false" locale="ru"></v-date-picker>
          </v-menu>
        </v-flex>

      </v-layout>

      <events-stats-timeline  transition="slide-x-transition" class="pt-3" v-bind:events="events"/>

    </v-card-text>
    <v-progress-linear
            :active="loading"
            :indeterminate="loading"
    ></v-progress-linear>
  </v-card>
</template>

<script>
  import api from "../api/api"
    import EventsStatsTimeline from "../components/EventsStatsTimeline";
    import moment from 'moment'
    export default {
        name: "Stats",
      components: {EventsStatsTimeline},
      data() {
        return {
          fromDate: new Date().toISOString().substr(0, 10),
          toDate: new Date().toISOString().substr(0, 10),
          value: 0,
          query: false,
          show: true,
          interval: 0,
          loading: false,
          loader: null,
          barcode: null,
          selected: [2],
          items: [],
          events: [],
          menu: false,
          modal: false,
          menu2: false

        }
      },
      mounted () {
        //this.getStats()
      },
      beforeDestroy () {
        clearInterval(this.interval)
      },
      methods: {
          today (){
            let now = moment();
            moment.lang('ru');
            return now.format('DD')
          },
        todayDay (){
          let now = moment();
          moment.lang('ru');
          return now.format('dddd')
        },

        todayMonth (){
          let now = moment();
          moment.lang('ru');
          return now.format('MMMM YYYYY')
        },

        getStats: function () {
            if (this.events != null) this.events.clear();
            this.loading = true;
            api.getStats({from: this.fromDate, to: this.toDate})
                  .then(stats => {
                    console.log(stats.data);
                    this.loading = false;
                    this.events = stats.data
                  })
                  .catch( err => {
                    this.loading = false;
                    console.log(err)
                  });

        }
      },
      computed: {
        fromDateFormattedMomentjs () {
          console.log(this.fromDate)
          return this.fromDate ? moment(this.fromDate).format('DD.MM.YYYY') : ''
        },
        toDateFormattedMomentjs () {
          return this.toDate ? moment(this.toDate).format('DD.MM.YYYY') : ''
        }}
    }
</script>
<style >
  table.v-table tbody td, table.v-table tbody th {
    height: 22px;
    width: 10px;
  }

</style>