<template>
  <v-container >
    <v-flex xs12 sm6 offset-sm3>
      <v-layout row>
    <v-text-field
            label="Штрихкод"
            autofocus
            v-model = "barcode"
            @keyup.enter.native="checkTicket()"
    ></v-text-field>
    <v-btn
          :loading="loading"
          :disabled="loading"
          color="secondary"
          @click.native="checkTicket()"
          fab
    >
      <v-icon>find_in_page</v-icon>
    </v-btn>
      </v-layout>

           <v-list dense>
            <template v-for="(item, index) in items">

              <v-subheader
                      v-if="item.event.dt"
                      :key="index"
              >
                {{ item.event.venue_title }}/{{ item.event.title }} <br>
                {{ frontEndDateFormat(item.event.dt) }}
              </v-subheader>
              <v-subheader
                      v-else
                      :key="index"
              >
                <small>{{item.ticket.barcode}} Билет не найден</small>
              </v-subheader>

              <v-list-tile
                      v-if="item.ticket.dt"
                      :key="item.ticket"
                      >
                <v-list-tile-avatar>
                  <img :src="require(`@/assets/allow.png`)">
                </v-list-tile-avatar>

                <v-list-tile-content >
                  <v-list-tile-title >{{ item.ticket.barcode }}</v-list-tile-title>
                  <v-list-tile-sub-title > {{ frontEndDateFormat(item.ticket.dt) }}</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile v-for="(ent, index) in item.entry" :key="index" >
                <v-icon v-if="ent.direction === 'entry' && ent.result_code === 1" class="light-blue--text">arrow_forward</v-icon>
                <v-icon v-else-if="ent.direction === 'exit' && ent.result_code === 1" class="green--text ">arrow_back</v-icon>
                <v-icon v-else class="red--text text--lighten-2">cancel</v-icon>
                <small>{{ frontEndDateFormat(ent.operation_dt) }}</small>&nbsp;{{ent.term.name}}
              </v-list-tile>
              <v-divider></v-divider>
            </template>
           </v-list>

    </v-flex>

  </v-container>
</template>

<script>
    import api from '../api/api'
    import moment from 'moment'
    export default {
        name: "Check",
        methods: {
          onBarcodeScanned (barcode) {
            if(barcode !== this.barcode) {
              this.barcode = barcode
              this.checkTicket()
            }

          },
            frontEndDateFormat: function(date) {
                moment.locale("ru");
                return moment.unix(date).format('DD/MM/YYYY HH:mm');
            },
            checkTicket: function () {
                this.loading = true;
                api.checkTicket({barcode: this.barcode})
                    .then(check => {
                        this.barcode = '';
                        this.items.unshift(check.data);
                        this.loading = false;
                    })
                    .catch( err => {
                        this.barcode = '';
                        this.loading = false;
                        this.loading = false;
                        console.log(err)
                    });

            }

        },
        data() {
            return {
                loading: false,
                loader: null,
                barcode: null,
                selected: [2],
                items: []
            }
            }
        ,
        mounted(){
        },
      created() {
        this.$barcodeScanner.init(this.onBarcodeScanned)
      },
      destroyed () {
        this.$barcodeScanner.destroy()
      },
    }
</script>
